'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useNotification } from '@/context/NotificationContext';
import { useRouter } from 'next/navigation';
import AdminSidebar from '@/components/AdminSidebar';
import AdminHeader from '@/components/AdminHeader';
import Loading from '@/components/Loading';
import api from '@/lib/api';
import { format } from 'date-fns';
import { 
  FiDollarSign, 
  FiCalendar, 
  FiUser, 
  FiFileText, 
  FiPlus,
  FiX,
  FiTrendingUp,
  FiTrendingDown,
  FiBriefcase
} from 'react-icons/fi';

export default function AdminPaymentsPage() {
  const { user, loading: authLoading, logout } = useAuth();
  const { showNotification } = useNotification();
  const router = useRouter();
  const [payments, setPayments] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [stats, setStats] = useState({
    totalRevenue: 0,
    totalCompanyShare: 0,
    totalDoctorPayments: 0,
    totalSalaries: 0,
    netProfit: 0,
  });
  const [filterType, setFilterType] = useState<'all' | 'salary' | 'fee_split'>('all');
  const [showSalaryModal, setShowSalaryModal] = useState(false);
  const [salaryForm, setSalaryForm] = useState({
    recipientId: '',
    amount: '',
    paymentMonth: '',
    notes: '',
  });
  const [staffList, setStaffList] = useState<any[]>([]);
  const [doctorsList, setDoctorsList] = useState<any[]>([]);

  useEffect(() => {
    if (!authLoading && !user) {
      router.push('/login');
      return;
    }
    if (user && user.role !== 'admin') {
      router.push('/dashboard');
      return;
    }
    fetchPayments();
    fetchStats();
    fetchStaffAndDoctors();
  }, [user, authLoading, router, filterType]);

  const fetchPayments = async () => {
    try {
      setLoading(true);
      const params: any = {};
      if (filterType !== 'all') {
        params.type = filterType;
      }
      const response = await api.get('/payments', { params });
      setPayments(response.data.payments || []);
    } catch (error) {
      console.error('Error fetching payments:', error);
      showNotification('Failed to fetch payments', 'error');
    } finally {
      setLoading(false);
    }
  };

  const fetchStats = async () => {
    try {
      const response = await api.get('/payments/stats');
      setStats(response.data.stats || {
        totalRevenue: 0,
        totalCompanyShare: 0,
        totalDoctorPayments: 0,
        totalSalaries: 0,
        netProfit: 0,
      });
    } catch (error) {
      console.error('Error fetching stats:', error);
    }
  };

  const fetchStaffAndDoctors = async () => {
    try {
      const [staffResponse, doctorsResponse] = await Promise.all([
        api.get('/staff'),
        api.get('/doctors'),
      ]);
      setStaffList(staffResponse.data.staff || []);
      setDoctorsList(doctorsResponse.data.doctors || []);
    } catch (error) {
      console.error('Error fetching staff/doctors:', error);
    }
  };

  const handlePaySalary = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      if (!salaryForm.recipientId || !salaryForm.amount || !salaryForm.paymentMonth) {
        showNotification('Please fill all required fields', 'error');
        return;
      }

      await api.post('/payments/salary', salaryForm);
      showNotification('Salary paid successfully', 'success');
      setShowSalaryModal(false);
      setSalaryForm({
        recipientId: '',
        amount: '',
        paymentMonth: '',
        notes: '',
      });
      fetchPayments();
      fetchStats();
    } catch (error: any) {
      console.error('Error paying salary:', error);
      showNotification(error.response?.data?.message || 'Failed to pay salary', 'error');
    }
  };

  const getPaymentTypeLabel = (type: string) => {
    switch (type) {
      case 'salary':
        return 'Salary';
      case 'fee_split':
        return 'Fee Split';
      case 'refund':
        return 'Refund';
      default:
        return type;
    }
  };

  const getPaymentTypeColor = (type: string) => {
    switch (type) {
      case 'salary':
        return 'bg-blue-100 text-blue-700';
      case 'fee_split':
        return 'bg-green-100 text-green-700';
      case 'refund':
        return 'bg-red-100 text-red-700';
      default:
        return 'bg-gray-100 text-gray-700';
    }
  };

  if (authLoading || loading) {
    return <Loading />;
  }

  if (!user || user.role !== 'admin') {
    return null;
  }

  const allRecipients = [
    ...staffList.map((s: any) => ({ id: s.userId, name: s.user?.name, type: 'staff' })),
    ...doctorsList.map((d: any) => ({ id: d.userId, name: d.user?.name, type: 'doctor' })),
  ];

  return (
    <div className="flex min-h-screen bg-gray-50">
      <AdminSidebar user={user} logout={logout} />
      <main className="w-full lg:ml-64 flex-1 transition-all duration-300">
        <header className="bg-teal-600 text-white shadow-xl">
          <div className="px-4 sm:px-6 lg:px-8 py-4 sm:py-6">
            <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-4">
              <div>
                <h1 className="text-2xl sm:text-3xl font-bold mb-2">Payment Management</h1>
                <p className="text-sm sm:text-base text-teal-100">Manage salaries and fee splits</p>
              </div>
              <button
                onClick={() => setShowSalaryModal(true)}
                className="flex items-center gap-2 px-6 py-3 bg-white text-teal-600 rounded-xl hover:bg-teal-50 transition-colors font-semibold shadow-lg"
              >
                <FiPlus /> Pay Salary
              </button>
            </div>
          </div>
        </header>

        <div className="p-4 sm:p-6 lg:p-8">
          {/* Stats Cards */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-6 mb-8">
            <div className="bg-white rounded-2xl shadow-xl p-6 border-2 border-gray-300 hover:shadow-2xl transition-all transform hover:scale-105">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-gray-600 mb-1">Total Revenue</p>
                  <p className="text-2xl font-bold text-gray-900">${stats.totalRevenue.toLocaleString()}</p>
                </div>
                <div className="w-12 h-12 rounded-full bg-green-100 flex items-center justify-center">
                  <FiTrendingUp className="text-2xl text-green-600" />
                </div>
              </div>
            </div>

            <div className="bg-white rounded-2xl shadow-xl p-6 border-2 border-gray-300 hover:shadow-2xl transition-all transform hover:scale-105">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-gray-600 mb-1">Company Share</p>
                  <p className="text-2xl font-bold text-teal-600">${stats.totalCompanyShare.toLocaleString()}</p>
                </div>
                <div className="w-12 h-12 rounded-full bg-teal-100 flex items-center justify-center">
                  <FiBriefcase className="text-2xl text-teal-600" />
                </div>
              </div>
            </div>

            <div className="bg-white rounded-2xl shadow-xl p-6 border-2 border-gray-300 hover:shadow-2xl transition-all transform hover:scale-105">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-gray-600 mb-1">Doctor Payments</p>
                  <p className="text-2xl font-bold text-blue-600">${stats.totalDoctorPayments.toLocaleString()}</p>
                </div>
                <div className="w-12 h-12 rounded-full bg-blue-100 flex items-center justify-center">
                  <FiUser className="text-2xl text-blue-600" />
                </div>
              </div>
            </div>

            <div className="bg-white rounded-2xl shadow-xl p-6 border-2 border-gray-300 hover:shadow-2xl transition-all transform hover:scale-105">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-gray-600 mb-1">Total Salaries</p>
                  <p className="text-2xl font-bold text-orange-600">${stats.totalSalaries.toLocaleString()}</p>
                </div>
                <div className="w-12 h-12 rounded-full bg-orange-100 flex items-center justify-center">
                  <FiDollarSign className="text-2xl text-orange-600" />
                </div>
              </div>
            </div>

            <div className="bg-white rounded-2xl shadow-xl p-6 border-2 border-gray-300 hover:shadow-2xl transition-all transform hover:scale-105">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-gray-600 mb-1">Net Profit</p>
                  <p className={`text-2xl font-bold ${stats.netProfit >= 0 ? 'text-green-600' : 'text-red-600'}`}>
                    ${stats.netProfit.toLocaleString()}
                  </p>
                </div>
                <div className={`w-12 h-12 rounded-full ${stats.netProfit >= 0 ? 'bg-green-100' : 'bg-red-100'} flex items-center justify-center`}>
                  <FiTrendingDown className={`text-2xl ${stats.netProfit >= 0 ? 'text-green-600' : 'text-red-600'}`} />
                </div>
              </div>
            </div>
          </div>

          {/* Filter Tabs */}
          <div className="mb-6">
            <div className="flex gap-2 border-b-2 border-gray-300">
              {(['all', 'salary', 'fee_split'] as const).map((tab) => (
                <button
                  key={tab}
                  onClick={() => setFilterType(tab)}
                  className={`px-6 py-3 font-medium transition-colors capitalize ${
                    filterType === tab
                      ? 'text-teal-600 border-b-2 border-teal-600'
                      : 'text-gray-600 hover:text-gray-900'
                  }`}
                >
                  {tab === 'all' ? 'All Payments' : tab === 'salary' ? 'Salaries' : 'Fee Splits'}
                </button>
              ))}
            </div>
          </div>

          {/* Payments List */}
          <div className="bg-white rounded-2xl shadow-xl p-6 border-2 border-gray-300 overflow-hidden">
            {payments.length === 0 ? (
              <div className="text-center py-12">
                <FiDollarSign className="text-6xl text-gray-300 mx-auto mb-4" />
                <h3 className="text-xl font-semibold text-gray-900 mb-2">No payments found</h3>
                <p className="text-gray-600">Payment records will appear here once transactions are made</p>
              </div>
            ) : (
              <div className="overflow-x-auto max-h-[600px] overflow-y-auto">
                <table className="w-full">
                  <thead className="bg-gray-50 sticky top-0">
                    <tr>
                      <th className="text-left py-3 px-4 font-semibold text-gray-700">Type</th>
                      <th className="text-left py-3 px-4 font-semibold text-gray-700">Recipient</th>
                      <th className="text-left py-3 px-4 font-semibold text-gray-700">Amount</th>
                      {filterType === 'fee_split' && (
                        <>
                          <th className="text-left py-3 px-4 font-semibold text-gray-700">Doctor Share (70%)</th>
                          <th className="text-left py-3 px-4 font-semibold text-gray-700">Company Share (30%)</th>
                          <th className="text-left py-3 px-4 font-semibold text-gray-700">Total Fee</th>
                        </>
                      )}
                      <th className="text-left py-3 px-4 font-semibold text-gray-700">Date</th>
                      <th className="text-left py-3 px-4 font-semibold text-gray-700">Status</th>
                    </tr>
                  </thead>
                  <tbody>
                    {payments.map((payment) => (
                      <tr key={payment.id} className="border-b border-gray-100 hover:bg-gray-50">
                        <td className="py-3 px-4">
                          <span className={`px-3 py-1 rounded-full text-xs font-semibold ${getPaymentTypeColor(payment.type)}`}>
                            {getPaymentTypeLabel(payment.type)}
                          </span>
                        </td>
                        <td className="py-3 px-4">
                          <div>
                            <div className="font-medium text-gray-900">{payment.recipient?.name || 'N/A'}</div>
                            {payment.doctor && (
                              <div className="text-sm text-gray-500">Dr. {payment.doctor.user?.name}</div>
                            )}
                          </div>
                        </td>
                        <td className="py-3 px-4 font-semibold text-gray-900">
                          ${parseFloat(payment.amount || 0).toLocaleString()}
                        </td>
                        {filterType === 'fee_split' && payment.type === 'fee_split' && (
                          <>
                            <td className="py-3 px-4 text-blue-600 font-medium">
                              ${parseFloat(payment.doctorShare || 0).toLocaleString()}
                            </td>
                            <td className="py-3 px-4 text-teal-600 font-medium">
                              ${parseFloat(payment.companyShare || 0).toLocaleString()}
                            </td>
                            <td className="py-3 px-4 text-gray-700 font-medium">
                              ${parseFloat(payment.totalFee || 0).toLocaleString()}
                            </td>
                          </>
                        )}
                        <td className="py-3 px-4 text-gray-600">
                          {format(new Date(payment.paymentDate), 'MMM dd, yyyy')}
                        </td>
                        <td className="py-3 px-4">
                          <span className={`px-3 py-1 rounded-full text-xs font-semibold ${
                            payment.status === 'completed' 
                              ? 'bg-green-100 text-green-700'
                              : payment.status === 'pending'
                              ? 'bg-yellow-100 text-yellow-700'
                              : 'bg-red-100 text-red-700'
                          }`}>
                            {payment.status}
                          </span>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            )}
          </div>
        </div>

        {/* Pay Salary Modal */}
        {showSalaryModal && (
          <div className="fixed inset-0 bg-black bg-opacity-60 backdrop-blur-sm z-50 flex items-center justify-center p-4 overflow-y-auto">
            <div className="bg-white rounded-2xl shadow-2xl max-w-md w-full border-2 border-gray-300 max-h-[90vh] overflow-y-auto">
              <div className="px-6 py-4 border-b border-gray-200 flex items-center justify-between sticky top-0 bg-white z-10">
                <h2 className="text-2xl font-bold text-gray-900">Pay Salary</h2>
                <button
                  onClick={() => {
                    setShowSalaryModal(false);
                    setSalaryForm({
                      recipientId: '',
                      amount: '',
                      paymentMonth: '',
                      notes: '',
                    });
                  }}
                  className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
                >
                  <FiX className="text-xl text-gray-600" />
                </button>
              </div>

              <form onSubmit={handlePaySalary} className="p-6 space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Recipient *</label>
                  <select
                    value={salaryForm.recipientId}
                    onChange={(e) => setSalaryForm({ ...salaryForm, recipientId: e.target.value })}
                    required
                    className="w-full px-4 py-3 border-2 border-gray-300 rounded-xl focus:ring-2 focus:ring-teal-500 focus:border-teal-500 outline-none"
                  >
                    <option value="">Select recipient</option>
                    {allRecipients.map((recipient) => (
                      <option key={recipient.id} value={recipient.id}>
                        {recipient.name} ({recipient.type === 'staff' ? 'Staff' : 'Doctor'})
                      </option>
                    ))}
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Amount *</label>
                  <input
                    type="number"
                    step="0.01"
                    value={salaryForm.amount}
                    onChange={(e) => setSalaryForm({ ...salaryForm, amount: e.target.value })}
                    required
                    className="w-full px-4 py-3 border-2 border-gray-300 rounded-xl focus:ring-2 focus:ring-teal-500 focus:border-teal-500 outline-none"
                    placeholder="0.00"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Payment Month *</label>
                  <input
                    type="month"
                    value={salaryForm.paymentMonth}
                    onChange={(e) => setSalaryForm({ ...salaryForm, paymentMonth: e.target.value })}
                    required
                    className="w-full px-4 py-3 border-2 border-gray-300 rounded-xl focus:ring-2 focus:ring-teal-500 focus:border-teal-500 outline-none"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Notes</label>
                  <textarea
                    value={salaryForm.notes}
                    onChange={(e) => setSalaryForm({ ...salaryForm, notes: e.target.value })}
                    rows={3}
                    className="w-full px-4 py-3 border-2 border-gray-300 rounded-xl focus:ring-2 focus:ring-teal-500 focus:border-teal-500 outline-none resize-none"
                    placeholder="Optional notes..."
                  />
                </div>

                <div className="flex gap-4 pt-4">
                  <button
                    type="submit"
                    className="flex-1 px-6 py-3 bg-teal-600 text-white rounded-xl hover:bg-teal-700 transition-colors font-semibold"
                  >
                    Pay Salary
                  </button>
                  <button
                    type="button"
                    onClick={() => {
                      setShowSalaryModal(false);
                      setSalaryForm({
                        recipientId: '',
                        amount: '',
                        paymentMonth: '',
                        notes: '',
                      });
                    }}
                    className="px-6 py-3 bg-gray-100 text-gray-700 rounded-xl hover:bg-gray-200 transition-colors font-semibold"
                  >
                    Cancel
                  </button>
                </div>
              </form>
            </div>
          </div>
        )}
      </main>
    </div>
  );
}
